import React from "react";
import { Platform, StyleSheet, Text, View } from "react-native";

const isVideoSrc = (src) => {
  if (!src) {
    return false;
  }
  const clean = String(src).split("?")[0];
  const ext = clean.split(".").pop().toLowerCase();
  return ["mp4", "webm", "ogv", "ogg", "mov", "m4v"].includes(ext);
};

const FullblockHeader = ({ data, desktopCompact = false }) => {
  if (!data?.src) {
    return null;
  }
  const isVideo = isVideoSrc(data.src);
  const isLight = Number(data?.textColor || 0) === 1;

  return (
    <View style={styles.container}>
      {Platform.OS === "web" ? (
        isVideo ? (
          <video
            src={data.src}
            autoPlay
            muted
            playsInline
            loop
            preload="metadata"
            style={StyleSheet.flatten([styles.media, desktopCompact ? styles.mediaDesktopCompact : null])}
          />
        ) : (
          <img src={data.src} alt="" style={StyleSheet.flatten([styles.media, desktopCompact ? styles.mediaDesktopCompact : null])} />
        )
      ) : (
        <View style={styles.mediaFallback} />
      )}
      <View style={styles.overlay} />
      {data?.text ? (
        <View style={styles.textWrap}>
          <Text style={[styles.text, isLight ? styles.textLight : styles.textDark]}>
            {data.text}
          </Text>
        </View>
      ) : null}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    position: "relative",
    width: "100%",
    overflow: "hidden",
    marginBottom: 10
  },
  media: {
    width: "100%",
    height: "auto",
    display: "block"
  },
  mediaDesktopCompact: {
    height: "50vh",
    objectFit: "cover"
  },
  mediaFallback: {
    width: "100%",
    height: 200,
    backgroundColor: "#e7e7e7"
  },
  overlay: {
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: "rgba(0,0,0,0.18)"
  },
  textWrap: {
    position: "absolute",
    left: 0,
    right: 0,
    top: "52%",
    transform: [{ translateY: -50 }],
    paddingHorizontal: "6%"
  },
  text: {
    fontWeight: "700",
    textAlign: "center",
    fontSize: 24,
    letterSpacing: 0.2
  },
  textLight: {
    color: "#ffffff",
    textShadowColor: "rgba(0,0,0,0.35)",
    textShadowOffset: { width: 0, height: 2 },
    textShadowRadius: 10
  },
  textDark: {
    color: "#101010"
  }
});

export default FullblockHeader;
