import React, { useEffect, useRef, useState } from "react";
import {
  Animated,
  Linking,
  Modal,
  Platform,
  Pressable,
  ScrollView,
  StyleSheet,
  Text,
  useWindowDimensions,
  View
} from "react-native";
import { useSelector } from "react-redux";
import { getTypeDevice } from "../../templateComponents/GetTypeDeviceSize/GetTypeDeviceSize";

const STORAGE_KEY = "stories_viewed_v1";
const DEFAULT_DURATION = 5000;
const MIN_DURATION = 500;

const isVideoSrc = (src) => {
  if (!src) {
    return false;
  }
  const clean = String(src).split("?")[0];
  const ext = clean.split(".").pop().toLowerCase();
  return ["mp4", "webm", "ogv", "ogg", "mov", "m4v"].includes(ext);
};

const readViewed = () => {
  if (typeof localStorage === "undefined") {
    return [];
  }
  try {
    const raw = localStorage.getItem(STORAGE_KEY) || "[]";
    const parsed = JSON.parse(raw);
    return Array.isArray(parsed) ? parsed : [];
  } catch (e) {
    return [];
  }
};

const writeViewed = (id) => {
  if (!id || typeof localStorage === "undefined") {
    return;
  }
  const viewed = readViewed();
  if (viewed.includes(id)) {
    return;
  }
  viewed.push(id);
  try {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(viewed));
  } catch (e) {
    return;
  }
};

const normalizeDuration = (duration) => {
  const parsed = parseInt(duration, 10);
  if (!parsed || Number.isNaN(parsed)) {
    return DEFAULT_DURATION;
  }
  return Math.max(MIN_DURATION, parsed);
};

const StoriesHeader = ({ data = [] }) => {
  const items = Array.isArray(data) ? data : [];
  const { width, height } = useWindowDimensions();
  const typeDeviceVK = useSelector(state => state.post.typeDeviceVK);
  const isMobileLayout = getTypeDevice(width, height, typeDeviceVK);
  const showDesktopArrows = Platform.OS === "web" && !isMobileLayout;
  const isIOSWeb = Platform.OS === "web" && /iPad|iPhone|iPod/.test((typeof navigator !== "undefined" && navigator.userAgent) || "");
  const [activeIndex, setActiveIndex] = useState(null);
  const [viewedMap, setViewedMap] = useState(() => new Set(readViewed()));
  const progress = useRef(new Animated.Value(0)).current;
  const storiesTrackRef = useRef(null);
  const mediaWidthRef = useRef(0);
  const videoRef = useRef(null);
  const durationRef = useRef(DEFAULT_DURATION);
  const progressRef = useRef(0);
  const remainingRef = useRef(DEFAULT_DURATION);
  const pausedRef = useRef(false);
  const trackDragActiveRef = useRef(false);
  const trackDragMovedRef = useRef(false);
  const trackDragStartXRef = useRef(0);
  const trackDragStartLeftRef = useRef(0);
  const trackScrollLeftRef = useRef(0);
  const hintAnimatedRef = useRef(false);
  const hintInProgressRef = useRef(false);
  const hintTimersRef = useRef([]);
  const [isTrackDragging, setIsTrackDragging] = useState(false);

  const closeStory = () => {
    setActiveIndex(null);
  };

  const markViewed = (id) => {
    if (!id) {
      return;
    }
    writeViewed(id);
    setViewedMap(new Set(readViewed()));
  };

  const goToIndex = (index) => {
    if (index < 0 || index >= items.length) {
      closeStory();
      return;
    }
    const story = items[index];
    markViewed(story?.id);
    setActiveIndex(index);
  };

  const goNext = () => {
    if (activeIndex === null) {
      goToIndex(0);
      return;
    }
    if (activeIndex >= items.length - 1) {
      closeStory();
      return;
    }
    goToIndex(activeIndex + 1);
  };

  const goPrev = () => {
    if (activeIndex === null) {
      return;
    }
    if (activeIndex <= 0) {
      return;
    }
    goToIndex(activeIndex - 1);
  };

  const resolveTapX = (event) => {
    const nativeEvent = event?.nativeEvent || {};
    if (typeof nativeEvent.locationX === "number") {
      return nativeEvent.locationX;
    }
    if (typeof nativeEvent.offsetX === "number") {
      return nativeEvent.offsetX;
    }
    const target = event?.currentTarget || nativeEvent?.target;
    if (target && typeof target.getBoundingClientRect === "function") {
      const rect = target.getBoundingClientRect();
      if (typeof nativeEvent.clientX === "number") {
        return nativeEvent.clientX - rect.left;
      }
      if (typeof nativeEvent.pageX === "number") {
        return nativeEvent.pageX - rect.left;
      }
    }
    return null;
  };

  const handleMediaPress = (event) => {
    const nativeEvent = event?.nativeEvent || {};
    const mediaWidth =
      mediaWidthRef.current ||
      nativeEvent?.target?.clientWidth ||
      nativeEvent?.target?.offsetWidth ||
      0;
    if (!mediaWidth) {
      return;
    }
    const clickX = resolveTapX(event);
    if (clickX === null) {
      return;
    }
    const ratio = clickX / mediaWidth;
    if (ratio <= 0.33) {
      goPrev();
    } else {
      goNext();
    }
  };

  const stopVideoPlayback = () => {
    const video = videoRef.current;
    if (!video || typeof video.pause !== "function") {
      return;
    }
    try {
      video.pause();
    } catch (e) {
      return;
    }
  };

  const playVideoPlayback = () => {
    const video = videoRef.current;
    if (!video || typeof video.play !== "function") {
      return;
    }
    try {
      video.play();
    } catch (e) {
      return;
    }
  };

  const freezePreviewVideo = (videoEl) => {
    if (!videoEl) {
      return;
    }
    try {
      videoEl.pause();
      if (typeof videoEl.currentTime === "number") {
        videoEl.currentTime = 0.01;
      }
    } catch (e) {
      return;
    }
  };

  const finishStory = (index) => {
    const story = items[index] || {};
    if (story?.autoNext) {
      goToIndex(index + 1);
    } else {
      closeStory();
    }
  };

  const startProgress = (index, fromProgress = 0) => {
    const bounded = Math.min(1, Math.max(0, fromProgress));
    progress.stopAnimation();
    progress.setValue(bounded);
    progressRef.current = bounded;
    remainingRef.current = Math.max(0, Math.round(durationRef.current * (1 - bounded)));

    if (remainingRef.current <= 0) {
      finishStory(index);
      return;
    }

    Animated.timing(progress, {
      toValue: 1,
      duration: remainingRef.current,
      useNativeDriver: false
    }).start(({ finished }) => {
      if (!finished) {
        return;
      }
      progressRef.current = 1;
      remainingRef.current = 0;
      finishStory(index);
    });
  };

  const pauseStory = () => {
    if (activeIndex === null || pausedRef.current) {
      return;
    }
    progress.stopAnimation((value) => {
      const current = typeof value === "number" ? value : progressRef.current;
      progressRef.current = current;
      remainingRef.current = Math.max(0, Math.round(durationRef.current * (1 - current)));
    });
    pausedRef.current = true;
    stopVideoPlayback();
  };

  const resumeStory = () => {
    if (activeIndex === null || !pausedRef.current) {
      return;
    }
    pausedRef.current = false;
    playVideoPlayback();
    startProgress(activeIndex, progressRef.current);
  };

  useEffect(() => {
    if (activeIndex === null) {
      progress.stopAnimation();
      progress.setValue(0);
      progressRef.current = 0;
      remainingRef.current = DEFAULT_DURATION;
      durationRef.current = DEFAULT_DURATION;
      pausedRef.current = false;
      stopVideoPlayback();
      return;
    }
    durationRef.current = normalizeDuration(items[activeIndex]?.duration);
    pausedRef.current = false;
    startProgress(activeIndex, 0);

    return () => {
      progress.stopAnimation();
    };
  }, [activeIndex, items, progress]);

  useEffect(() => {
    if (Platform.OS !== "web" || activeIndex === null) {
      return;
    }
    const onKeyDown = (event) => {
      if (event?.key === "Escape") {
        closeStory();
      }
    };
    window.addEventListener("keydown", onKeyDown);
    return () => {
      window.removeEventListener("keydown", onKeyDown);
    };
  }, [activeIndex]);

  useEffect(() => {
    if (Platform.OS !== "web" || !items.length || activeIndex !== null || hintAnimatedRef.current) {
      return () => {};
    }

    clearHintTimers();

    const probeDelays = [450, 900, 1600, 2600];
    probeDelays.forEach((delay) => {
      const probeTimer = setTimeout(() => runTrackHintAnimation(), delay);
      hintTimersRef.current.push(probeTimer);
    });

    return () => {
      clearHintTimers();
    };
  }, [items.length, width, height, activeIndex]);

  const handleStoriesWheel = (event) => {
    if (Platform.OS !== "web") {
      return;
    }
    const nativeEvent = event?.nativeEvent || {};
    const deltaY = nativeEvent?.deltaY ?? event?.deltaY ?? 0;
    const deltaX = nativeEvent?.deltaX ?? event?.deltaX ?? 0;
    if (Math.abs(deltaY) <= Math.abs(deltaX)) {
      return;
    }
    if (deltaY !== 0) {
      scrollTrackTo(null, getCurrentTrackLeft() + deltaY);
      if (typeof event.preventDefault === "function") {
        event.preventDefault();
      }
    }
  };

  const getTrackNode = () => {
    return (
      (storiesTrackRef.current &&
        typeof storiesTrackRef.current.getScrollableNode === "function" &&
        storiesTrackRef.current.getScrollableNode()) ||
      storiesTrackRef.current
    );
  };

  const clearHintTimers = () => {
    hintTimersRef.current.forEach((timerId) => clearTimeout(timerId));
    hintTimersRef.current = [];
  };

  const scrollTrackTo = (node, left) => {
    const targetLeft = Math.max(0, Number(left) || 0);
    trackScrollLeftRef.current = targetLeft;
    const trackNode = node || getTrackNode();
    if (trackNode && typeof trackNode.scrollLeft === "number") {
      if (typeof trackNode.scrollTo === "function") {
        try {
          trackNode.scrollTo({ left: targetLeft, behavior: "auto" });
          return;
        } catch (e) {
          // fallback below
        }
      }
      trackNode.scrollLeft = targetLeft;
      return;
    }
    const trackRef = storiesTrackRef.current;
    if (trackRef && typeof trackRef.scrollTo === "function") {
      try {
        trackRef.scrollTo({ x: targetLeft, y: 0, animated: false });
        return;
      } catch (e) {
        return;
      }
    }
  };

  const getCurrentTrackLeft = () => {
    const node = getTrackNode();
    if (node && typeof node.scrollLeft === "number") {
      return node.scrollLeft || 0;
    }
    return trackScrollLeftRef.current || 0;
  };

  const runTrackHintAnimation = () => {
    if (hintAnimatedRef.current || hintInProgressRef.current) {
      return;
    }
    const node = getTrackNode();
    const startLeft = getCurrentTrackLeft();
    const shift = isIOSWeb ? 48 : 56;
    hintAnimatedRef.current = true;
    hintInProgressRef.current = true;

    scrollTrackTo(node, startLeft + shift);

    const timerBack = setTimeout(() => {
      scrollTrackTo(node, startLeft);
    }, isIOSWeb ? 360 : 280);

    const timerFinish = setTimeout(() => {
      hintInProgressRef.current = false;
    }, isIOSWeb ? 760 : 620);

    hintTimersRef.current.push(timerBack, timerFinish);
  };

  const scrollTrackBy = (delta) => {
    scrollTrackTo(null, getCurrentTrackLeft() + delta);
  };

  const startTrackDrag = (event) => {
    if (Platform.OS !== "web") {
      return;
    }
    const node = getTrackNode();
    if (!node) {
      return;
    }
    const nativeEvent = event?.nativeEvent || {};
    const startX =
      typeof nativeEvent.pageX === "number"
        ? nativeEvent.pageX
        : typeof nativeEvent.clientX === "number"
          ? nativeEvent.clientX
          : null;
    if (startX === null) {
      return;
    }
    trackDragActiveRef.current = true;
    trackDragMovedRef.current = false;
    trackDragStartXRef.current = startX;
    trackDragStartLeftRef.current = node.scrollLeft || 0;
    setIsTrackDragging(true);
  };

  const moveTrackDrag = (event) => {
    if (Platform.OS !== "web" || !trackDragActiveRef.current) {
      return;
    }
    const node = getTrackNode();
    if (!node) {
      return;
    }
    const nativeEvent = event?.nativeEvent || {};
    const currentX =
      typeof nativeEvent.pageX === "number"
        ? nativeEvent.pageX
        : typeof nativeEvent.clientX === "number"
          ? nativeEvent.clientX
          : null;
    if (currentX === null) {
      return;
    }
    const delta = currentX - trackDragStartXRef.current;
    if (Math.abs(delta) > 6) {
      trackDragMovedRef.current = true;
    }
    node.scrollLeft = trackDragStartLeftRef.current - delta;
    if (typeof event.preventDefault === "function") {
      event.preventDefault();
    }
  };

  const endTrackDrag = () => {
    if (!trackDragActiveRef.current) {
      return;
    }
    trackDragActiveRef.current = false;
    setIsTrackDragging(false);
    setTimeout(() => {
      trackDragMovedRef.current = false;
    }, 0);
  };

  if (!items.length) {
    return null;
  }

  const activeStory = activeIndex !== null ? items[activeIndex] : null;
  const activeIsVideo = activeStory ? isVideoSrc(activeStory.src) : false;
  const modalMediaMaxHeight = Math.max(200, height - 120);
  const modalMediaMaxWidth = Math.max(280, Math.min(width - 24, 720));
  const modalMediaStyle = StyleSheet.flatten([
    styles.modalMedia,
    { width: modalMediaMaxWidth, maxHeight: modalMediaMaxHeight }
  ]);

  return (
    <View style={styles.container}>
      <View
        style={styles.trackWrap}
        onMouseDown={startTrackDrag}
        onMouseMove={moveTrackDrag}
        onMouseUp={endTrackDrag}
        onMouseLeave={endTrackDrag}
      >
        {showDesktopArrows ? (
          <Pressable style={[styles.trackArrow, styles.trackArrowLeft]} onPress={() => scrollTrackBy(-260)}>
            <Text style={styles.trackArrowText}></Text>
          </Pressable>
        ) : null}
        <ScrollView
          ref={storiesTrackRef}
          horizontal
          showsHorizontalScrollIndicator={false}
          contentContainerStyle={styles.track}
          onWheel={handleStoriesWheel}
          onScroll={(event) => {
            const xOffset = event?.nativeEvent?.contentOffset?.x;
            if (typeof xOffset === "number") {
              trackScrollLeftRef.current = xOffset;
              return;
            }
            const currentTarget = event?.currentTarget;
            if (currentTarget && typeof currentTarget.scrollLeft === "number") {
              trackScrollLeftRef.current = currentTarget.scrollLeft;
            }
          }}
          scrollEventThrottle={16}
          style={isTrackDragging ? styles.trackDragging : null}
        >
          {items.map((story, index) => {
            const isViewed = viewedMap.has(story?.id);
            const previewIsVideo = isVideoSrc(story?.src);
            return (
              <Pressable
                key={`story_${story?.id || index}`}
                style={[styles.card, !isViewed ? styles.cardUnviewed : null]}
                onPress={() => {
                  if (trackDragMovedRef.current) {
                    return;
                  }
                  goToIndex(index);
                }}
              >
                {Platform.OS === "web" ? (
                  previewIsVideo ? (
                    <video
                      src={story?.src}
                      muted
                      playsInline
                      preload="metadata"
                      autoPlay={false}
                      loop={false}
                      draggable={false}
                      ref={freezePreviewVideo}
                      onLoadedMetadata={(event) => freezePreviewVideo(event?.currentTarget)}
                      onLoadedData={(event) => freezePreviewVideo(event?.currentTarget)}
                      onDragStart={(event) => event.preventDefault()}
                      style={styles.cardMedia}
                    />
                  ) : (
                    <img
                      src={story?.src}
                      alt=""
                      draggable={false}
                      onDragStart={(event) => event.preventDefault()}
                      style={styles.cardMedia}
                    />
                  )
                ) : (
                  <View style={styles.cardMediaFallback} />
                )}
              </Pressable>
            );
          })}
        </ScrollView>
        {showDesktopArrows ? (
          <Pressable style={[styles.trackArrow, styles.trackArrowRight]} onPress={() => scrollTrackBy(260)}>
            <Text style={styles.trackArrowText}></Text>
          </Pressable>
        ) : null}
      </View>

      {activeStory ? (
        <Modal
          visible
          transparent
          animationType="fade"
          onRequestClose={closeStory}
        >
          <View style={styles.modal}>
            <Pressable style={styles.backdrop} onPress={closeStory} />
            <View style={styles.modalTopBar}>
              <View style={styles.progressWrap}>
                <View style={styles.progress}>
                  {items.map((_, idx) => (
                    <View key={`seg_${idx}`} style={styles.progressSegment}>
                      {idx < activeIndex ? (
                        <View style={styles.progressFillDone} />
                      ) : null}
                      {idx === activeIndex ? (
                        <Animated.View
                          style={[
                            styles.progressFill,
                            {
                              width: progress.interpolate({
                                inputRange: [0, 1],
                                outputRange: ["0%", "100%"]
                              })
                            }
                          ]}
                        />
                      ) : null}
                    </View>
                  ))}
                </View>
                <Pressable style={styles.closeButton} onPress={closeStory}>
                  <Text style={styles.closeButtonText}></Text>
                </Pressable>
              </View>
            </View>
            <View style={styles.modalContent}>
              <Pressable
                style={styles.modalMediaTapArea}
                onLayout={(event) => {
                  mediaWidthRef.current = event.nativeEvent.layout.width || 0;
                }}
                onPressIn={pauseStory}
                onPressOut={resumeStory}
                onPress={handleMediaPress}
              >
                {Platform.OS === "web" ? (
                  activeIsVideo ? (
                    <video
                      ref={videoRef}
                      src={activeStory?.src}
                      autoPlay
                      muted
                      playsInline
                      preload="metadata"
                      draggable={false}
                      onDragStart={(event) => event.preventDefault()}
                      onContextMenu={(event) => event.preventDefault()}
                      onLoadedMetadata={(event) => {
                        const videoDuration = event?.currentTarget?.duration;
                        const durationMs = normalizeDuration(Math.round((videoDuration || 0) * 1000));
                        if (durationMs !== durationRef.current) {
                          durationRef.current = durationMs;
                          pausedRef.current = false;
                          startProgress(activeIndex, 0);
                        }
                      }}
                      style={modalMediaStyle}
                    />
                  ) : (
                    <img
                      src={activeStory?.src}
                      alt=""
                      draggable={false}
                      onDragStart={(event) => event.preventDefault()}
                      style={modalMediaStyle}
                      onContextMenu={(event) => event.preventDefault()}
                    />
                  )
                ) : (
                  <View style={styles.cardMediaFallback} />
                )}
              </Pressable>
              {activeStory?.link ? (
                <Pressable
                  style={styles.linkButton}
                  onPress={() => Linking.openURL(activeStory.link)}
                >
                  <Text style={styles.linkButtonText}></Text>
                </Pressable>
              ) : null}
            </View>
          </View>
        </Modal>
      ) : null}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    paddingHorizontal: 12,
    marginTop: 4,
    marginBottom: 10,
    userSelect: "none"
  },
  track: {
    gap: 10,
    paddingHorizontal: 4
  },
  trackWrap: {
    position: "relative",
    userSelect: "none"
  },
  trackDragging: {
    cursor: "grabbing"
  },
  trackArrow: {
    position: "absolute",
    top: "50%",
    marginTop: -16,
    width: 32,
    height: 32,
    borderRadius: 16,
    backgroundColor: "rgba(17,17,17,0.6)",
    alignItems: "center",
    justifyContent: "center",
    zIndex: 2
  },
  trackArrowLeft: {
    left: 0
  },
  trackArrowRight: {
    right: 0
  },
  trackArrowText: {
    color: "#fff",
    fontSize: 24,
    lineHeight: 24,
    marginTop: -2,
    userSelect: "none"
  },
  card: {
    width: 132,
    height: 176,
    borderRadius: 16,
    overflow: "hidden",
    backgroundColor: "#f2f2f2",
    boxShadow: "0 6px 12px rgba(0,0,0,0.12)"
  },
  cardUnviewed: {
    borderWidth: 2,
    borderColor: "#ff7a00"
  },
  cardMedia: {
    width: "100%",
    height: "100%",
    objectFit: "cover",
    display: "block",
    userSelect: "none",
    WebkitUserDrag: "none"
  },
  cardMediaFallback: {
    width: "100%",
    height: "100%",
    backgroundColor: "#dcdcdc"
  },
  modal: {
    flex: 1,
    zIndex: 999999,
    justifyContent: "center",
    alignItems: "center"
  },
  backdrop: {
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: "rgba(0,0,0,0.7)"
  },
  modalContent: {
    width: "100%",
    height: "100%",
    paddingTop: 44,
    paddingBottom: 16,
    paddingHorizontal: 12,
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "transparent",
    zIndex: 1
  },
  modalTopBar: {
    position: "absolute",
    top: 10,
    left: 12,
    right: 12,
    zIndex: 2
  },
  modalMediaTapArea: {
    width: "100%",
    flex: 1,
    alignItems: "center",
    justifyContent: "center"
  },
  modalMedia: {
    objectFit: "contain",
    borderRadius: 16,
    boxShadow: "0 10px 30px rgba(0,0,0,0.25)",
    userSelect: "none",
    WebkitUserDrag: "none"
  },
  progressWrap: {
    flexDirection: "row",
    alignItems: "center",
    gap: 10
  },
  progress: {
    flex: 1,
    flexDirection: "row",
    gap: 6
  },
  progressSegment: {
    flex: 1,
    height: 4,
    backgroundColor: "rgba(255,255,255,0.35)",
    borderRadius: 999,
    overflow: "hidden"
  },
  progressFill: {
    height: "100%",
    backgroundColor: "#ffffff"
  },
  progressFillDone: {
    height: "100%",
    width: "100%",
    backgroundColor: "#ffffff"
  },
  closeButton: {
    width: 30,
    height: 30,
    borderRadius: 15,
    backgroundColor: "rgba(0,0,0,0.4)",
    alignItems: "center",
    justifyContent: "center"
  },
  closeButtonText: {
    color: "#fff",
    fontSize: 22,
    lineHeight: 22
  },
  linkButton: {
    alignSelf: "center",
    marginTop: 12,
    paddingVertical: 8,
    paddingHorizontal: 16,
    backgroundColor: "#ffffff",
    borderRadius: 999
  },
  linkButtonText: {
    color: "#111111",
    fontSize: 14
  }
});

export default StoriesHeader;
